body {
  display: flex;
  flex-direction: column;
  margin: 0;
  margin-top: 68px;
  margin-bottom: 70px;
  justify-content: center;
  /* 가로 중앙 정렬 */
  align-items: center;
  /* 세로 중앙 정렬 */
  background-color: #F2F2F7;
  /* 배경색 지정 */
  /* src/App.css */
  font-family: 'Pretendard', sans-serif;


}

.top-title {
  width: 357px;
  display: flex;
  justify-content: space-between; /* 요소를 가로 방향으로 최대한 넓게 배치합니다. */
  align-items: center; /* 요소를 수직 방향으로 가운데로 정렬합니다. */
}

.top {
  font-size: 30px;
  font-weight: 700;
}

.upload-button {
  font-size: 12px;
  width: 68px;
  height: 31px;
  border-radius: 2px;
  background: #007AFF;
  color: white;
  border: none;
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: center;
}

.search {
  display: flex;
  width: 357px;
  height: 30px;
  margin-top: 10px;
}

form {
  display: flex;
  width: 357px;
  /* 원하는 너비로 조정 */
  height: 34px;
  /* 원하는 높이로 조정 */
  border: none;
  border-radius: 8px;
  background-color: #E3E3E8;
}

input {
  border: none;
  background: none;
  width: 319px;
}

