}



.information-box {
  all: unset;
  margin-top: 10px;
  padding-left: 20px;
  padding-right: 20px;
  padding-top: 28px;
  padding-bottom: 28px;
  width: 318px;
  height: 112px;
  border-radius: 8px;
  background-color: white;
}

.information-box:hover {
  background: #D8D8D8;
}

#information-box-admin {
  background-color: white;
}



.information-box-top {
  display: flex;
  justify-content: space-between;
  /* 내부 요소를 양쪽 끝으로 정렬 */
  width: 100%;
  /* 필요에 따라 너비를 조정 */
}

.information_company_name {
  font-size: 12px;
  font-weight: 300;
}

.information_employment_deadline {
  font-size: 12px;
  font-weight: 700;
  color: #007AFF;
}

.information-box-title {
  white-space: nowrap;
  /* 줄 바꿈 방지 */
  overflow: hidden;
  /* 넘치는 부분 숨김 */
  text-overflow: ellipsis;
  /* 생략 부호(...)로 표시 */
  width: 316px;
  /* 필요에 따라 너비 조정 */
  font-size: 14px;
  font-weight: 700;
}

.information-box-location {
