}
.condition-one, .condition-two, .condition-three, .condition-four , .detail-one, .detail-two, .detail-three , .contact-one, .contact-two {
  display: flex;
  margin-top: 5px;
}

.condition_one {
  font-size: 14px;
  font-weight: 500;
  color: #828282;
  width: 67px;
}
.condition_two {
  font-size: 14px;
  font-weight: 500;
}


.detail_one , .contact_one {
  width: 67px;
