}



#upload-content, #upload-content-2  {
  margin-top: 4px;
  box-sizing: border-box;
  height: 38px;
  border: solid 1px #EEEEEF;
  border-radius: 2px;
  font-size: 14px;
  padding-left: 10px;
  padding-right: 10px;
}

#upload-content-2 {
  margin-top: 4px;
  box-sizing: border-box;
  height: 38px;
  border: solid 1px #EEEEEF;
  border-radius: 2px;
  font-size: 14px;
  padding-left: 10px;
  padding-right: 10px;
}


.checkbox-group {
  height: 76px;
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
  margin-top: 5px;
}

label {
  width: 69.5px;
  height: 30px;
  border: solid 1px #EEEEEF;
  border-radius: 2px;
  font-size: 10px;
  display: flex;
  align-items: center;
  gap: 8px; /* 체크박스와 텍스트 사이의 간격을 조정할 수 있습니다. */
}

input[type="checkbox"] {
  margin-left: 10px;
  appearance: none;
  width: 15px;
  height: 15px;
  border: 1px solid #EEEEEF;
  border-radius: 200px;
  outline: none;
}

input[type="checkbox"]:checked {
  background-color: #2196F3; /* 체크된 경우의 배경색을 지정합니다. */
}

.none-label {
  border: none;
}

.submit-button {
  margin-top: 20px;
  border: none;
  background-color: #007AFF;
  color: white;
  border-radius: 2px;
  font-size: 14px;
  font-weight: 700;
  height: 38px;
}

.submit-button:hover {
  cursor: pointer;
}

.login-button {
  all: unset;
  color: #007AFF;
  font-size: 14px;
  font-weight: 700;
}

.email-input-box, .password-input-box {
  align-items: center;
  all: unset;
  font-size: 14px;
  width: 295px;
  height: 38px;
  padding-left: 10px;
  padding-right: 10px;
  border: solid 1px #EEEEEF;
  border-radius: 2px;
  margin-bottom: 5px;
}

.login-button-pop {
  height: 38px;
  background: #007AFF;
  font-size: 14px;
  font-weight: 700;
  color: white;
  border: none;
  border-radius: 2px;
  margin-top: 15px;
  margin-bottom: 5px;
  cursor: pointer;
}

.close-button-pop {
  border: none;
  background-color: white;
  margin-top: 5px;
  font-size: 12px;
  cursor: pointer;
}

.approval-button, .delete-button, .edit-button {
  border: none;
  width: 80px;
  height: 35px;
  margin-right: 10px;
  border-radius: 2px;
}

.approval-button {
  background: #007AFF;
  color: white;
  font-weight: 700;
}

.edit-button {
  background: white;
  border-radius: 2px;
  border: solid 1px #BDBDBD;
}

.cancel-button {
  border: none;
  width: 80px;
  height: 35px;
  margin-right: 10px;
  border-radius: 2px;
  background: white;
  border: solid 1px #BDBDBD;
}



.delete-button {
  background: red;
  color: white;
  font-weight: 700;
}

.admin-button-under {
  margin-top: 5px;
}


.filter {
  margin-top: 10px;
}

.filter_One, .filter_Two {
  width: 100px;
}

.filter_Two {
  margin-left: 10px;
}